@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Montserrat';
  background-color: #2c3e50;
  color: white;
}

a {
  text-decoration: none;
  color: white;
  cursor: pointer;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #222;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #666;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #444;
}

.feed {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  padding: 0 40px;
  grid-gap: 10px;
  gap: 10px;
  margin-bottom: 30px;
}

.tweet {
  display: flex;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  border-radius: 12px;
  background: rgb(0 0 0 / 5%);
  box-shadow: 0 4px 12px -1px rgb(0 0 0 / 50%);
  transition: all 0.2s;
}

.tweet:hover {
  transform: scale(1.02);
}

.tweet-content,
.tweet-author {
  padding: 0 20px;
}

.tweet-author {
  align-self: flex-end;
}

.header {
  display: flex;
  align-items: center;
  height: 120px;
  background: rgb(0 0 0 / 15%);
  box-shadow: 2px 2px 20px -1px rgb(0 0 0 / 50%);
  padding: 0 40px;
  margin: 0;
}

.header-title {
  font-size: 48px;
}

.navlinks {
  width: 100%;
  display: flex;
  grid-gap: 40px;
  gap: 40px;
  justify-content: flex-end;
}

.header-title,
.navlink {
  transition: all 0.2s;
}

.header-title:hover,
.navlink:hover {
  color: #e67e22;
}

@media screen and (max-width: 600px) {
  .header-title {
    font-size: 24px;
  }

  .navlink {
    font-size: 12px;
  }
}

.post-tweet {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgb(0 0 0 / 20%);
  height: 100px;
  box-shadow: 0 5px 20px -1px rgb(0 0 0 / 15%);
  border-radius: 0 0 12px 12px;
  padding: 0 40px;
}

form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-self: center;
  grid-gap: 20px;
  gap: 20px;
}

.post-a-tweet {
  background: rgb(0 0 0 / 15%);
  border: none;
  outline: none;
  padding: 20px;
  width: 100%;
  font-family: 'Montserrat';
  color: white;
}

.fweet-button {
  cursor: pointer;
  padding: 15px 20px;
  font-family: 'Montserrat';
  border: none;
  outline: none;
  transition: ease-in-out 0.2s;
  background: #e67e22;
  color: white;
}

.fweet-button:hover {
  transform: scale(1.1);
}

.register-form-container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.register-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 75vh;
  width: 50%;
  background: rgb(0 0 0 / 20%);
  border-radius: 12px;
  box-shadow: 0 2px 6px -1px rgb(0 0 0 / 50%);
}

.register-input {
  border: none;
  outline: none;
  padding: 15px;
  font-family: 'Montserrat';
  background: rgb(0 0 0 / 40%);
  color: white;
  width: 50%;
}

.submit-button {
  padding: 15px;
  border: none;
  outline: none;
  font-family: 'Montserrat';
  background: rgb(0 0 0 / 60%);
  color: white;
  cursor: pointer;
  transition: all 0.2s;
}

.submit-button:hover {
  transform: scale(1.04);
  color: #e67e22;
}

.already-account {
  font-size: 12px;
  transition: all 0.2s;
}

.already-account:hover {
  color: #e67e22;
}

.signin-form-container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.signin-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 50vh;
  width: 50%;
  background: rgb(0 0 0 / 20%);
  border-radius: 12px;
  box-shadow: 0 2px 6px -1px rgb(0 0 0 / 50%);
}

.signin-input {
  border: none;
  outline: none;
  padding: 15px;
  font-family: 'Montserrat';
  background: rgb(0 0 0 / 40%);
  color: white;
  width: 50%;
}

.submit-button {
  padding: 15px;
  border: none;
  outline: none;
  font-family: 'Montserrat';
  margin-top: 12px;
  background: rgb(0 0 0 / 60%);
  color: white;
  cursor: pointer;
  transition: all 0.2s;
}

.submit-button:hover {
  transform: scale(1.04);
  color: #e67e22;
}

.already-account {
  font-size: 12px;
  transition: all 0.2s;
}

.already-account:hover {
  color: #e67e22;
}

